import React from 'react';
import { PrismLight as SyntaxHighlighter } from 'react-syntax-highlighter';
import jsx from 'react-syntax-highlighter/dist/cjs/languages/prism/jsx';
import tsx from 'react-syntax-highlighter/dist/cjs/languages/prism/tsx';
import rust from 'react-syntax-highlighter/dist/cjs/languages/prism/rust';
import bash from 'react-syntax-highlighter/dist/cjs/languages/prism/bash';
// Remove this, once `react-syntax-highlighter` is updated to support `toml` lang
import toml from 'refractor/lang/toml';
import atomDark from 'react-syntax-highlighter/dist/cjs/styles/prism/atom-dark';
import { makeStyles, Theme } from '@material-ui/core';

SyntaxHighlighter.registerLanguage('js', jsx);
SyntaxHighlighter.registerLanguage('ts', tsx);
SyntaxHighlighter.registerLanguage('rust', rust);
SyntaxHighlighter.registerLanguage('toml', toml);
SyntaxHighlighter.registerLanguage('bash', bash);

export interface ICodeBlockProps {
  showLines?: boolean;
  className: string;
  children: React.ReactChild;
}

const useStyles = makeStyles((theme: Theme) => ({
  root: {
    marginTop: 16,
    marginBottom: 48,
    padding: 0,
    borderRadius: 4,
    background: theme.palette.primary.main,
  },
  windowBlock: {
    position: 'relative',
    padding: 16,
    borderRadius: 4,
    boxShadow: 'rgba(0, 0, 0, 0.55) 0px 10px 32px',
  },
  windowControls: {
    position: 'relative',
    paddingBottom: 8,
  },
  codeBlock: {
    padding: '0 !important',
  },
}));

export default function CodeBlock({
  children,
  className,
  showLines = false,
}: ICodeBlockProps) {
  const classes = useStyles();
  const details = className.replace(/language-/, '');
  // eslint-disable-next-line prefer-const
  let [language, linesEnabledStr] = details.split('.');
  const linesEnabled = /true/i.test(linesEnabledStr);
  switch (language) {
    case 'rs':
      language = 'rust';
      break;
    case 'sh':
    case 'bash':
      language = 'bash';
      break;
  }
  return (
    <div className={classes.root}>
      <div
        className={classes.windowBlock}
        style={{
          background:
            atomDark[':not(pre) > code[class*="language-"]'].background,
        }}
      >
        <div className={classes.windowControls}>
          <svg
            xmlns="http://www.w3.org/2000/svg"
            width="54"
            height="14"
            viewBox="0 0 54 14"
          >
            <g fill="none" fillRule="evenodd" transform="translate(1 1)">
              <circle
                cx="6"
                cy="6"
                r="6"
                fill="#FF5F56"
                stroke="#E0443E"
                strokeWidth=".5"
              ></circle>
              <circle
                cx="26"
                cy="6"
                r="6"
                fill="#FFBD2E"
                stroke="#DEA123"
                strokeWidth=".5"
              ></circle>
              <circle
                cx="46"
                cy="6"
                r="6"
                fill="#27C93F"
                stroke="#1AAB29"
                strokeWidth=".5"
              ></circle>
            </g>
          </svg>
        </div>
        <SyntaxHighlighter
          language={language}
          className={classes.codeBlock}
          style={atomDark}
          showLineNumbers={linesEnabledStr == null ? showLines : linesEnabled}
        >
          {children}
        </SyntaxHighlighter>
      </div>
    </div>
  );
}
